.slideshow-home{
    max-width: 1920px;
    margin: 0px auto;
    min-height: 110px;
    max-height: 450px;
    overflow: hidden;
    @media (min-width: 768px) {
        min-height: 422px;
    }
    @media (max-width: 768px) {
        max-height: 130px;
        overflow: hidden;
    }
    .info-slider{
        position: absolute;
        color: #fff;
        top: 30%;
        left: 8%;
        h4{
            font-size: 36px;
            font-weight: bold;
            text-transform: uppercase;
            margin-bottom: 30px;
            text-shadow: 2px 2px #00000082;
        }
        p{
            a{
                font-weight: bold;
                font-size: 18px;
                text-transform: uppercase;
                color: #fff;
                background: #d2aa41;
                border: 2px solid #fff;
                padding: 17px 20px;
                display: inline-block;
            }
        }
    }
    .owl-carousel{
        .item{
            img{
                height: auto;
            }
        }
    }
    .owl-dots {
        position: absolute;
        width: 100%;
        bottom: 23px;
        text-align: center;
        .owl-dot{
            display: inline-block;
            margin-right: 12px;
            width: 13px;
            height: 13px;
            background: #ccc;
            border-radius: 50%;
        }
        .owl-dot.active{
            background: #ed1b68;
        }
    }

}

.swiper-wrapper{
    .swiper-slide{
        .vn-testimonies-avatar {
            .image-student-b{
                width: 120px;
                height: 120px;
                overflow: hidden;
                border-radius: 50%;
                border: 4px solid #0067b8;
                display: block;
                margin: 0 auto;
                .border-img-show {
                    display: block;
                    margin: 0 auto;
                }
            }
            .comment-studen {
                height: 100px;
                overflow: hidden;
            }
        }
        .comment-studen {
            &:before {
                content: url(../images/member-comment.png);
                float: left;
                margin-right: 15px;
            }
        }
    }
    .owl-dots{
        width: 100%;
        display: flex;
        justify-content: center;
        .owl-dot {
            display: inline-block;
            border-radius: 100%;
            background-color: #fff;
            border: 1px solid #d3b771;
            width: 12px;
            height: 12px;
            opacity: 1;
            margin: 0 5px;
            &.active {
                opacity: 1;
                background: #04a5f0;
                border: 1px solid #04a5f0;
            }
        }
    }
}

